
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import StatList from '@/components/attendance/StatList'
import LingLoading from '@/components/attendance/LingLoading'
export default {
name: 'PrincipalAttendanceHome',
components: {
Group,
XHeader,
Cell,
StatList,
Flexbox,
FlexboxItem,
XButton,
LingLoading
},
data() {
return {
selectTime: '',
statList: [
{ label: "总人数", number: 99 },
{ label: "实到", number: 99 },
{ label: "未到", number: 99 }
],
statList1: [
{ label: "请假", number: 1 },
],
statList2: [
{ label: "请假", number: 3 },
{ label: "发热", number: 2 }
],
}
},
methods: {
fetchData: function() {

this.$store.dispatch('actionSchoolAttendance', {date: '2017-03-06', schoolId: 1})
}
},
computed: {
/**
* 老师统计信息
* @return {Array} 组装的老师统计信息
*/
getTeacherStatList() {
let arr = [], info = this.$store.getters.getSchoolAttendance
arr.push({label: '总人数', number: info.allTeacherNum})
arr.push({label: '实到', number: info.teacherArriveNum})
arr.push({label: '未到', number: info.teacherNoArriveNum})
return arr
},
/**
* 学生统计信息
* @return {Array} 组装的学生统计信息
*/
getStudentStatList() {
let arr = [], info = this.$store.getters.getSchoolAttendance
arr.push({label: '总人数', number: info.allStudentNum})
arr.push({label: '实到', number: info.studentArriveNum})
arr.push({label: '未到', number: info.studentNoArriveNum})
return arr
},
/**
* 获取，取数据的状态
* @returns {getLingLoading}
*/
getLoadingState: function() {
return this.$store.getters.getLingLoading
},
/**
* 获取选择时间
* @returns {*}
*/
getSelectTime: function() {
return this.$parent.$data.selectTime
}
},
watch: {
/**
* 监听选择时间的变化
* @param val
*/
getSelectTime: function(val) {
window.console.log(val)
}
},
created() {
this.fetchData()
}
}



